@charset "UTF-8";
/*
  * Reset CSS
*/
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* list Remove default style */
ul,
ol {
  padding: 0;
  list-style: none;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizespeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* フォームリセット */
input,
button,
textarea,
select {
  font: inherit;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  outline: none;
}

/* stylelint-disable */
textarea {
  resize: vertical;
}

/* stylelint-enable */
input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

/* =====================
 * Baseスタイル
===================== */
:root,
:root[theme=Default] {
  --f-family-main: "游ゴシック体", "YuGothic", "Yu Gothic", "メイリオ", "Meiryo",
    "Hiragino Kaku Gothic ProN", "ヒラギノ角ゴ ProN W3", sans-serif;
  --f-family-sub: "oswald", sans-serif;
  --f-font-size: 16px;
  --f-line-height: 1.6;
  --f-weight-regular: 400;
  --f-weight-medium: 500;
  --f-weight-semibold: 600;
  --f-weight-bold: 700;
  --f-weight-black: 900;
  --c-main: #fff;
  --c-sub: #272727;
  --c-sub-rgb-opacity25: rgb(39 39 39 / 25%);
  --c-sub-rgb-opacity40: rgb(39 39 39 / 40%);
  --c-sub-rgb-opacity60: rgb(39 39 39 / 60%);
  --c-red: #ff0303;
  --c-accent: #ccc;
  --c-accent-opacity-40: rgb(202 202 202 / 40%);
  --c-gray: #8c8c8c;
  --c-green: #08aa9f;
  --c-twitter: #000;
  --c-github: #1a1414;
}

html {
  /* ----------------------------
  * リキッドレイアウト rootのfont-sizeを可変にして、単位をremで指定することでレイアウトを可変にする
    // 計算式参考サイト：https://lpeg.info/html/css_calc_font.html
    // font-size: calc([最小値px] + ([最大値] - [最小値]) * ((100vw - [最小画面幅px]) / ([最大画面幅] - [最小画面幅])));
    // clampと併用して上限下限を設定すること。しないと無限に変化し続ける
  * ---------------------------- */
}
@media (max-width: 375px) {
  html {
    font-size: clamp(12px, 4.2666666667vw, 16px);
  }
}
@media (min-width: 375px) {
  html {
    font-size: 16px;
  }
}
@media (min-width: 768px) {
  html {
    font-size: clamp(12px, 12px + 4 * (100vw - 768px) / 232, 16px);
  }
}
@media (min-width: 1000px) {
  html {
    font-size: 16px;
  }
}

body {
  font-family: var(--f-family-main);
  font-size: var(--f-font-size);
  font-weight: var(--f-weight-medium);
  line-height: var(--f-line-height);
  background-color: var(--c-main);
  color: var(--c-sub);
  overflow-wrap: break-word;
  word-break: break-all;
}

img {
  height: auto;
  vertical-align: top;
}

a {
  display: inline-block;
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}
@media (min-width: 768px) {
  a:hover, a:focus {
    opacity: 0.7;
  }
}

.l-container {
  width: 100%;
  margin: 0 auto;
  padding: 0 1.25rem;
  background-color: #ddd;
}
@media (min-width: 768px) {
  .l-container {
    max-width: 1060px;
    padding: 0 1.875rem;
  }
}

.c-test {
  font-size: 1rem;
}

.p-test {
  font-size: 1.875rem;
}

@media (min-width: 768px) {
  .u-pc-hidden {
    display: none;
  }
}

.u-space-contact {
  margin-top: 7.5rem;
}
@media (min-width: 768px) {
  .u-space-contact {
    margin-top: 10rem;
  }
}